import { highlightSyntax } from '@/business/terminalHighlight'

interface LineProps {
	input: string
	onInputChange: (value: string) => void
	onKeyPress: (e: React.KeyboardEvent) => void
}

export default function Line({ input, onInputChange, onKeyPress }: LineProps) {
	return (
		<div className='flex items-center'>
			<span className='text-blue-400'>visitor@portfolio</span>
			<span className='text-white'>:</span>
			<span className='text-cyan-400'>~</span>
			<span className='text-white'>$</span>

			<div className='ml-2 flex-1 relative'>
				<div className='absolute inset-0 font-mono text-white pointer-events-none'>
					{highlightSyntax(input)}
					<span className='ml-1 text-yellow-300 animate-pulse'>_</span>
				</div>

				<input
					type='text'
					value={input}
					onChange={e => onInputChange(e.target.value)}
					onKeyPress={onKeyPress}
					className='w-full bg-transparent text-transparent outline-none font-mono caret-transparent'
					autoFocus
					spellCheck={false}
				/>
			</div>
		</div>
	)
}
